import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";

import { Search } from "react-iconly";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import AddVersionfeatures from "./addVersionfeatures​";
import {
  getVersionfeatures,
  versionfeaturesEdit,
  versionfeaturesDelete,
  isModelVisible,
  SetVersionfeaturesParams,
} from "../../../../store/Masters/Versionfeatures​/versionfeatures​Slice";

function Versionfeatures(props) {
  useEffect(() => {
    props.getVersionfeatures({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Version Feature",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_versionfeature") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.versionfeaturesEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_versionfeature") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.versionfeaturesDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.versionfeatureslist.map((row, key) => ({
    ...row,
    index:
      (props.versionfeaturesParams.page - 1) *
        props.versionfeaturesParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetVersionfeaturesParams({
      ...props.versionfeaturesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getVersionfeatures({
      ...props.versionfeaturesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetVersionfeaturesParams({
      ...props.versionfeaturesParams,
      search: val,
    });
    props.getVersionfeatures({ ...props.versionfeaturesParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;

  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Version Feature Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_versionfeature") !== -1 &&
          "Add Version Feature"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-26">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getVersionfeatures({
                      ...props.versionfeaturesParams,
                      page: 1,
                      search: "",
                    });
                    props.SetVersionfeaturesParams({
                      ...props.versionfeaturesParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.versionfeaturesParams?.page}
                pageSize={props.versionfeaturesParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddVersionfeatures />
    </Row>
  );
}

function mapStateToProps({ versionfeatures, system }) {
  return {
    versionfeatureslist: versionfeatures.versionfeatureslist,
    noofpages: versionfeatures.noofpages,
    listCount: versionfeatures.listCount,
    versionfeaturesParams: versionfeatures.versionfeaturesParams,
    loading: versionfeatures.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getVersionfeatures,
  versionfeaturesEdit,
  versionfeaturesDelete,
  isModelVisible,
  SetVersionfeaturesParams,
})(Versionfeatures);
